<template>
  <p class="note note-warning mt-4">
    <strong>{{ enemy["name"] }}</strong>
  </p>
  <div
    class="wave-button"
    v-for="i in wavelength + 1"
    :key="i"
    @click="setWave(i - 1)"
  >
    <img
      src="images/map-current.png"
      alt=""
      style="position: absolute; transform: scale(0.7)"
      class="marker"
      v-if="this.trueNumber == i - 1"
    />
    <img src="images/profile/NightChick.png" alt="" />
  </div>
  <div class="stage">
    <MDBBtn
      tag="a"
      color="primary"
      floating
      size="lg"
      style="background-color: rgb(172, 43, 172)"
      :class="{ disabled: activeNumber == 0 }"
      @click="waveDecrease"
    >
      <MDBIcon icon="angle-left" iconStyle="fas" size="3x" />
    </MDBBtn>

    <enemy-wave :wave="activeWave" />
    <MDBBtn
      tag="a"
      color="primary"
      floating
      size="lg"
      style="background-color: rgb(172, 43, 172)"
      :class="{ disabled: activeNumber >= wavelength }"
      @click="waveIncrease"
    >
      <MDBIcon icon="angle-right" iconStyle="fas" size="3x" />
    </MDBBtn>
  </div>
</template>

<script>
import { MDBIcon, MDBBtn } from "mdb-vue-ui-kit";
import EnemyWave from "../components/EnemyWave.vue";
export default {
  name: "EnemyStage",
  props: {
    enemy: Object,
  },
  components: {
    EnemyWave,
    MDBIcon,
    MDBBtn,
  },
  computed: {
    wave() {
      return this.enemy["wave"];
    },
    wavelength() {
      return this.wave.length - 1;
    },
    activeWave() {
      return this.wave[this.trueNumber];
    },
    trueNumber() {
      if (this.activeNumber > this.wavelength) {
        this.setWave(this.wavelength);
      }
      if (this.activeNumber < 0) {
        this.setWave(0);
      }
      return this.activeNumber;
    },
  },
  data() {
    return { activeNumber: 0 };
  },
  methods: {
    waveIncrease() {
      if (this.activeNumber < this.wavelength) {
        this.activeNumber++;
      }
    },
    waveDecrease() {
      if (this.activeNumber > 0) {
        this.activeNumber--;
      }
    },
    setWave(i) {
      this.activeNumber = i;
    },
  },
};
</script>

<style>
.stage > * {
  vertical-align: middle;
}
.wave-button {
  margin: 0.5em;
  display: inline-block;
  cursor: pointer;
}
.wave-button img {
  width: 42px;
}
</style>
